<template>
  <div class="content">
    <info :parentData="userInfo"></info>
    <div class="account_off" @click="accountOff">账户注销</div>
    <div class="account_off">{{version}}</div>
    <div @click="signOut" class="signOut">退出</div>
  </div>
</template>

<script>
/*
 * 接口配置文件
*/
// import personalApi from '@/http/service/index';
/**
 * vant框架
 */
import Vue from 'vue';
import { Dialog, Toast } from 'vant';

/*
 * 个人信息
*/
import info from './info';

/**
 * 获取版本号
 */
import { version } from '../../../../package.json'
// 全局注册
Vue.use({ Dialog });

/*
 * 个人中心 设置页面
*/
export default {
  name: 'setUp',
  components: {
    info
  },
  data () {
    return {
      userInfo: {
        storeName: '',
        mobile: '',
        pic: '',
      },
      version: `V-${version}`
    }
  },
  methods: {

    /**
     * @description 退出
     */
    signOut () {
      const context = this;
      Dialog.confirm({
        title: '提示',
        message: '确认退出登录?',
        className: 'dialog-custom',
      }).then(() => {
        // on confirm
        context.$store.dispatch('logout', '/')
      }).catch(() => {
        // on cancel
      });
    },
    accountOff () {
      Toast('请前往智家掌柜APP进行账户注销');
    }
  },
}
</script>

